<template>
  <div class="">
    <v-container grid-list-md>
      <h3>Entries Found</h3>
      <v-data-table
        :headers="headers"
        :items="$store.state.entries.entries"
        class="elevation-1"
      >
        <template slot="items" slot-scope="props">
          <td>{{ props.item.booktitle }}</td>
          <td>{{ props.item.entryType }}</td>
          <td>{{ props.item.year }}</td>
          <!-- <td><v-btn :to="{ name: 'e', params: { id:  props.item._id  }}" color="info">Details</v-btn></td> -->
        </template>
      </v-data-table>
    </v-container>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';

import HELPERS from '../helpers';

/* eslint no-unused-vars: ["error", {"args": "none"}] */
/* eslint no-console: ["error", { allow: ["log"] }] */

export default {
  mixins: [HELPERS],
  components: {
  },
  data() {
    return {
      view: {},
      data: [],
      headers: [
        { text: 'booktitle', value: 'booktitle' },
        { text: 'entryType', value: 'entryType' },
        { text: 'year', value: 'year' },
      ],
    };
  },
  methods: {
    ...mapMutations('entries', [
      'setEntry',
    ]),
  },
  computed: {
  },
  created() {
    this.get({ type: 'Collect', id:this.$route.params.id }).then((res) => {
      this.view = res.data;
    });
    this.get({ type: 'Inventory', query:`{"partOf":"${this.$route.params.id}"}` }).then((res) => {
      this.data = res.data;
    });
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



